<template>
    <div id="resolve">
        <el-row>
            <el-col :span="24">
                <el-card shadow="always">
                    <div class="title"><span class="iconfont icon-icon-up"></span>{{title}}</div>
                    <el-form :model="resolve">
                        <div class="sjgh">
                            <div class="sj"><span class="left">上级规划：</span><p class="right">{{resolve.sj.mc}}</p></div>
                            <div class="sj"><span class="left">上级领导：</span><p class="right">{{resolve.leader}}</p></div>
                        </div>
                        <el-form-item label="规划名称：">
                            <el-input v-model="resolve.mc" ref="ghmc" type="textarea" resize="none" autosize placeholder="请输入规划名称"></el-input>
                        </el-form-item>
                        <div class="form_item">
                            <el-form-item label="负责人：" class="fzr">
                                <!-- <el-select v-model="resolve.fzr" @change="fzr($event)" filterable="" default-first-option placeholder="请选择负责人" style="width:100%;">
                                    <el-option
                                    v-for="item in options"
                                    :key="item.user_id"
                                    :label="item.name"
                                    :value="item.user_id">
                                    </el-option> -->
                                    <el-col :span="12" style="width:100%;padding:0;">
                                        <el-autocomplete
                                        class="inline-input"
                                        v-model="resolve.fzr"
                                        :fetch-suggestions="queSearch"
                                        placeholder="请选择负责人"
                                        @select="fzr"
                                        clearable>
                                        </el-autocomplete>
                                    </el-col>
                                <!-- </el-select> -->
                            </el-form-item>
                            <el-form-item label="经费预算：" class="jfys">
                                <el-input v-model="resolve.jfys" ref="jf" type="number" placeholder="请输入金额" autocomplete="off">
                                    <template slot="append">万元</template>
                                </el-input>
                            </el-form-item>
                            <el-form-item label="紧急程度：" class="important">
                                <el-select v-model="resolve.important" @change="jjcd" placeholder="请选择项目重要程度" style="width:100%;">
                                    <el-option
                                    v-for="item in option2"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-form-item>
                            <el-form-item label="分解权重：" class="quanzhong">
                                <el-input v-model="input" ref="qz" placeholder="请输入权重大小" onkeyup="this.value=this.value.replace(/[^\-?\d.]/g,'')"></el-input>
                            </el-form-item>
                        </div>
                        <div class="form_item">
                            <el-form-item label="开始时间：" class="starrttime">
                                <el-date-picker
                                    v-model="resolve.start"
                                    type="date"
                                    placeholder="选择开始日期">
                                </el-date-picker>
                            </el-form-item>
                            <el-form-item label="结束时间：" class="endtime">
                                <el-date-picker
                                    v-model="resolve.end"
                                    type="date"
                                    placeholder="选择结束日期">
                                </el-date-picker>
                            </el-form-item>
                        </div>
                        <el-form-item label="参与人：">
                            <el-select v-model="resolve.cyr" @change="cyz" multiple filterable="" default-first-option placeholder="请选择参与人" style="width:100%;">
                                <el-option v-for="item in options" :key="item.user_id" :label="item.name" :value="item.user_id" :disabled="item.name===resolve.fzr?true:false">
                                </el-option>
                            </el-select>
                        </el-form-item>
                        <div class="khbz">
                            <el-form-item label="考核指标：" style="width:33%;margin-right:35px;">
                                <el-col :span="12" style="width:100%;padding:0;">
                                    <el-autocomplete
                                    class="inline-input"
                                    v-model="resolve.zgzb"
                                    :fetch-suggestions="querySearch"
                                    placeholder="请输入内容"
                                    @select="handleSelect"
                                    clearable>
                                    </el-autocomplete>
                                </el-col>
                            </el-form-item>
                            <el-form-item label="诊改目标：" style="flex:1;">
                                <div class="zhengaimubiao">
                                    <el-select v-model="resolve.mark" @change="fh" class="mb_mark" placeholder="请选择">
                                        <el-option
                                        v-for="item in option1"
                                        :key="item.value"
                                        :label="item.label"
                                        :value="item.value">
                                        </el-option>
                                    </el-select>
                                    <el-input class="mb_ipt1" ref="mb" v-model="resolve.zgmb" placeholder="请输入目标" autocomplete="off"></el-input>
                                </div>
                            </el-form-item>
                        </div>
                        <el-form-item label="规划简介：">
                            <el-input v-model="resolve.jj" ref="jjnr" type="textarea" :autosize="{ minRows: 6}" placeholder="请输入简介内容"></el-input>
                        </el-form-item>
                    </el-form>
                    <div class="resolve-footer">
                        <el-button type="primary" @click="confirm()">确 认</el-button>
                        <el-button @click="cancel()">返 回</el-button>
                    </div>
                </el-card>
            </el-col>
        </el-row>
    </div>
</template>

<script>
import { resolve } from 'url';
export default {
    name:'resolve',
    inject:["reload"],
    pickerOptions1:"2019-4-19",
    data(){
        return{
            input:"",
            title: '分解项目节点',
            resolve: {
                title: '规划项目编辑',
                level: '',
                id: '',
                mc:'',
                important: '',
                quanzhong: '5',
                leader: '李老师',
                sj: {gh: '上级规划',mc: '上级规划名称上级规划名称',ldid:''},
                start: '',
                end: '',
                jfys: null,
                fzr: '',
                fid:'',
                cyr: [],
                zgzb: '',
                zgzbID: '',
                //zgbz: '',
                mark: '',
                zgmb:'',
                jd: null,
                jj: '',
                target_dj:'',
            },
            rules:{
                khzb: [
                    { required: true, message: '请输入考核指标', trigger: 'change' }
                ],
                start: [
                    { required: true, message: '请选择开始时间', trigger: 'change' }
                ],
                end: [
                    { required: true, message: '请选择结束时间', trigger: 'change' }
                ],
            },
            options:[{
                value:'张老师',
                label:'张老师'
            },{
                value:'李老师',
                label:'李老师'
            },{
                value:'周老师',
                label:'周老师'
            },{
                value:'王老师',
                label:'王老师'
            },{
                value:'赵老师',
                label:'赵老师'
            }],
            option1: [{
                value: '1',
                label: '＞'
            },{
                value: '2',
                label: '≥'
            },{
                value: '3',
                label: '＜'
            },{
                value: '4',
                label: '≤'
            },{
                value: '5',
                label: '＝'
            },{
                value: '6',
                label: '≠'
            }],
            option2: [
                {
                    value: '1',
                    label: '重要'
                },{
                    value: '2',
                    label: '普通'
                },{
                    value: '3',
                    label: '暂缓'
                }
            ],
            option3: [
                {
                    value: '0',
                    label: '0'
                },
                {
                    value: '1',
                    label: '1'
                },
                {
                    value: '2',
                    label: '2'
                },
                {
                    value: '3',
                    label: '3'
                },
                {
                    value: '4',
                    label: '4'
                },
                {
                    value: '5',
                    label: '5'
                },
                {
                    value: '6',
                    label: '6'
                },
                {
                    value: '7',
                    label: '7'
                },
                {
                    value: '8',
                    label: '8'
                },
                {
                    value: '9',
                    label: '9'
                },
            ],
            restaurants:[],
            restaurants1:[],
            

        }
    },
    methods:{
        querySearch(queryString, cb) {
            var restaurants = this.restaurants;
            var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants;
            // 调用 callback 返回建议列表的数据
            cb(results);
        },
        queSearch(queryString, cb) {
            var restaurants1 = this.restaurants1;
            var results = queryString ? restaurants1.filter(this.createFilter(queryString)) : restaurants1;
            // 调用 callback 返回建议列表的数据
            cb(results);
        },
        createFilter(queryString) {
                return (restaurant) => {
                return (restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0);
            };
        },
        // loadAll() {
        //     return [
        //         { "value": "就业率" },
        //         { "value": "双师型比例" },
        //         { "value": "新生增长率" },
        //     ];
        // },
        handleSelect(item) {

            this.resolve.zgzbID = item.id
            console.log(item);
        },
        fzr(val){
            localStorage.setItem("fid",val.id)
            console.log(val)
        },
        cyz(e){
            localStorage.setItem("cyz",e)
        },
        jjcd(value){
            localStorage.setItem("jjcd",value)
            console.log(value)
        },
        fh(value){
            localStorage.setItem("fh",value)
        },
        confirm(){
            this.$confirm('确认分解吗？')
            .then(_ => {
                if(this.resolve.start && this.resolve.end){
                var d_start = new Date(this.resolve.start)
                var d_end = new Date(this.resolve.end)
                var s_m = d_start.getMonth()
                var s_d = d_start.getDate()
                var e_m = d_end.getMonth()
                var e_d = d_end.getDate()
                console.log(s_m,s_d)
                if(s_m + 1 < 10){
                    s_m = "0" + (s_m + 1)
                }else{
                    s_m = s_m + 1
                }
                if(s_d < 10){
                    s_d = "0" + s_d
                }
                if(e_m + 1 < 10){
                    e_m = "0" + (e_m + 1)
                }else{
                    e_m = e_m + 1
                }
                if(e_d < 10){
                    e_d = "0" + e_d
                }
                this.resolve.start = d_start.getFullYear() + '-' + s_m + '-' + s_d
                this.resolve.end = d_end.getFullYear() + '-' + e_m + '-' + e_d  
            }
            
            var that = this;
            console.log(this.$route.query)
            var xfid = this.$route.query.fid;
            var fdj = this.$route.query.fdj;
            console.log(fid+fdj)
            var cyr = localStorage.getItem("cyz");
            var id = localStorage.getItem("fjid");
            var ghmc = this.$refs.ghmc.value;
            var fid = localStorage.getItem("fid");
            var jf = this.$refs.jf.value;
            var jjcd = localStorage.getItem("jjcd");
            var qz = this.$refs.qz.value;
            var khbz = this.resolve.zgzbID;
            var xmjj = this.$refs.jjnr.value;
            var fh = localStorage.getItem("fh");
            var mb = this.$refs.mb.value;
            var jjnr = this.$refs.jjnr.value;//简介内容
            var parent_name =this.resolve.sj.mc;
            var creator_id =this.resolve.sj.ldid;
            console.log(this.resolve.target_dj)
            var dj =parseInt(this.resolve.target_dj)+parseInt(1);
            var start_time =this.resolve.start;
            var end_time =this.resolve.end;
            var target_category = sessionStorage.getItem('target_category');
            var target_extends = sessionStorage.getItem('target_extends');
            console.log('等级'+dj)
            $.ajax({
                type: "post",
                url: this.baseUrl+"/ydjxzg/mb/decompose.action",
                data: {
                    target_extends: target_extends,
                    target_category: target_category,
                    userlist:cyr,//参与人
                    parent_id:id,//项目id
                    target_name:ghmc,//项目名
                    manager_id:fid,//负责人ID
                    Funds:jf,//项目经费
                    Importancedj:jjcd,  //重要程度
                    Weight:qz,  //权重
                    start_time:start_time,  //开始时间
                    end_time:end_time,  //结束时间
                    target_info:xmjj, //项目描述
                    target_stand:khbz, //考核标准
                    target_stand_contion:fh, //1 大于等于 2大于 3等于 4小于等于 5小于 6不等于
                    target_stand_info:mb, //考核标准描述
                    parent_name:parent_name,
                    target_dj:dj,
                    creator_id:creator_id
                },
                dataType: "json",
                success: function (res) {
                    console.log(123)
                    console.log(res)
                    // 将数据上传到后台分配任务
                    //that.$confirm('确认分配到“'+that.resolve.fzr+'”吗？')
                    //.then(_ => {
                        // 向后台发起请求将this.resolve的值发送过去     根据level和id进行分配任务
                        that.$message({
                            message: '分配成功',
                            type: 'success'
                        });
                        console.log(fid+fdj)
                        // that.$router.push({path: "/tree?id="+xfid+"&dj="+fdj})
                        // that.reload()
                        that.$router.push({path: "/zgmb"});
                        that.reload();
                }
                    })
            })
            .catch(_ => {
                        this.$message({
                            message: '已取消',
                            type: 'warning'
                        });
                    });
        },
        cancel(){
            this.$confirm('确认返回到上一页吗？')
            .then(_ => {
                this.$router.go(-1)
            })
            .catch(_ => {});
        }
    },
    mounted() {
        
        //根据传过来的id和level获取项目的level id 项目名称 负责人(上级领导)赋值给this.resolve
        console.log(this.$route.query)
        localStorage.setItem("fjid",this.$route.query.id)
        this.resolve.level = this.$route.query.level
        this.resolve.id = this.$route.query.id
        if(this.resolve.level == 1){
            this.title = '分解诊改工作规划'
        }else if(this.resolve.level == 2){
            this.title = '分解专项工作规划'
        }else if(this.resolve.level == 3){
            this.title = '分解项目清单'
        }else if(this.resolve.level == 4){
            this.title = '分解工作任务'
        };
        var zgzb=[];
        var fzyli=[];
        var that = this;
        var id = localStorage.getItem("fjid")
        console.log(id)
        $.ajax({
            type: "post",
            url: this.baseUrl+"/ydjxzg/mb/goDecompose.action",
            data: {
                targetid:id
            },
            dataType: "json",
            success: function (res) {
                console.log(res, 'rrrrrres')
                sessionStorage.setItem('target_extends', res.target.target_extends);
                sessionStorage.setItem('target_category', res.target.target_category);
                if(res.khzb){
                        for(var i = 0;i < res.khzb.length;i++){
                            zgzb.push({id:res.khzb[i].id,value:res.khzb[i].zbmc})
                        }
                        console.log(zgzb)
                    }
                    if(res.userlist){
                        for(var i = 0;i < res.userlist.length;i++){
                            fzyli.push({id:res.userlist[i].user_id,value:res.userlist[i].name})
                        }
                    }
                     console.log(fzyli)
                that.resolve.sj.mc = res.target.target_name;
                that.resolve.sj.ldid=res.target.manager_id;
                that.resolve.leader=res.target.manager_name;
                //that.resolve.start=res.target.start_time;
                that.resolve.start=new Date();
                //that.resolve.end=res.target.end_time;
                var now = new Date();
                that.resolve.end=now.setMonth(now.getMonth()+1);
                that.resolve.target_dj=res.target.target_dj;
                that.options = res.userlist;
            }
        });
        this.restaurants = zgzb;
        this.restaurants1 = fzyli;
    },
}
</script>

<style>
#resolve{
    width: 100%;
    height: 100%;
    margin-top: 20px;
}
#resolve .el-row{
    padding: 0 10px;
}
#resolve .el-row .el-card__body{
    padding: 50px 0;
}
#resolve .title{
    width: 82%;
    height: auto;
    line-height: 40px;
    font-size: 24px;
    margin: 0 auto;
}
#resolve .title .iconfont{
    margin-left: -16px;
}
#resolve .el-form{
    padding: 0 8%;
}
#resolve .el-form .sjgh{
    font-size: 14px;
    color: #606266;
    line-height: 40px;
}
#resolve .el-form .sjgh .sj{
    display: flex;
}
#resolve .el-form .sjgh .left{
    display: inline-block;
}
#resolve .el-form .sjgh .right{
    display: inline-block;
    flex: 1;
    height: auto;
}
#resolve .form_item{
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
@media all and (max-width: 1200px) {
    #resolve .form_item .fzr,#resolve .form_item .jfys,#resolve .form_item .important,#resolve .form_item .quanzhong{
        width: 21%;
    }
}
#resolve .form_item .fzr,#resolve .form_item .jfys,#resolve .form_item .important,#resolve .form_item .quanzhong{
    width: 23%;
}
#resolve .form_item .starrttime,#resolve .form_item .endtime{
    width: 45%;
}
#resolve .form_item .el-date-editor{
    width: 100%;
}
#resolve .el-form-item .el-form-item__label{
    display: block;
    float: none;
    text-align: left;
}
#resolve .inline-input{
    width: 100%!important; 
}
#resolve .zhengaimubiao{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
#resolve .khbz{
    width: 100%;
    display: flex;
    justify-content: space-between;
}
#resolve .mb_mark{
    width: 49%;
    margin-right:15px;
}
#resolve .mb_ipt1{
    width: 49%;
}
#resolve .iconfont{
    font-weight: 900;
}
#resolve .resolve-footer{
    width: 84%;
    padding: 0 8%;
}
#addProject .resolve-footer .el-button--default{
    background: #fdfdfd;
}
#resolve .btn-custom-cancel {
    float: right;
    margin-left: 10px;
}
body .el-table th.gutter{
    display: table-cell!important;
}
input::-webkit-outer-spin-button,  
input::-webkit-inner-spin-button{  
    -webkit-appearance: none !important;  
    margin: 0;  
}
</style>


